'use strict';

import core from "src/public/vendor/metaljs/src/core";
import SoyComponent from "src/public/vendor/metaljs/src/soy/SoyComponent";

class Component extends SoyComponent {
  constructor(opt_config) {
    super(opt_config);
  }

  syncVisible(visible) {
    this.element.style.display = visible ? null : 'none';
  }
}

Component.ATTRS = {
  visible: {
    validator: core.isBoolean,
    value: true
  }
};

export default Component;
